@import url('https://fonts.googleapis.com/css2?family=Syne+Tactile&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Unica+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');

$display-font: 'Unica One', cursive;
$button-font: 'Syne Tactile', cursive;
$titles-font: 'Bungee', cursive;

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
#app{
  min-height: 100%;
  
  display: flex;
  justify-content: center;
  align-items: center;
  
  background-image: url('https://i.ibb.co/txFskb2/Ukiyo-e.jpg');
  
  #drum-machine{
    width: 360px;
    background: rgba(218, 153, 80, 0.49);

    .drum-kit{
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
    

    .drum-pad{
      font-size: 30px;
      font-family: $button-font;
      color: rgba(109, 90, 62, 1);
      
      margin: 10px;
      width: 100px;
      height: 100px;      
      
      border: none;
      border-radius: 50%;
      
      background-color: rgba(217, 210, 80, 1);
      
    }
    .drum-pad:hover{
        background-color: rgba(227, 217, 35, 1);
    }
    .drum-pad:active{
        background-color: rgba(166, 159, 38, 1);
    }
  }

  #display {    
    width: 200px;    
    display: block;
    margin: 20px;
    margin-left: auto;
    margin-right: auto;
    
    background-color: rgba(196, 63, 21, 1);
    p{
      margin: 0;
      padding:5px;
      
      text-align: center;
      font-family: $titles-font;
      color: rgba(243, 230, 212, 1);
    }
    .display {      
      min-height: 40px;
      
      display: flex;
      flex-direction: row;
      justify-content: center;
      
      background-color: rgba(243, 230, 212, 1);
        p{
          align-self: center;
          font-family: $display-font;
          color: rgba(109, 90, 62, 1);
        }
      }
    }
    }
}




